@charset "utf-8";

// Our variables
$base-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-size:   16px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #2a7ae2;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;



// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
    "partials/license",
    "partials/vendors/vendors",
    "partials/palette",
    "partials/mixins",
    "partials/variables",
    "base",
    "layout",
    "partials/boilerplate_first",
    "partials/media_query",
    "partials/base",
    "partials/layout",
    "partials/typography",
    "partials/z-index",
    "partials/main-menu",
    "partials/footer",
    "partials/slider",
    "partials/intro",
    "partials/skill",
    "partials/list",
    "partials/portfolio",
    "partials/blog",
    "partials/form",
    "partials/tool-tip",
    "partials/light-box",
    "partials/common",
    "partials/icons",
    "partials/animation",
    "partials/browser-update",
    "partials/helper"
;
